import MainButton from "../../components/buttons/MainButton";
import Layout from "../../components/layouts/Layout";
import SimplePricing from "../../components/pricings/SimplePricing";
import { useParams } from "react-router-dom";
import products from "./products.json";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";

const DetailProduct = () => {
  const { slug } = useParams();

  const product = products.filter((product) => product.productId === slug)[0];

  return (
    <Layout header={true} footer={true}>
      <section className="text-gray-700 body-font overflow-hidden bg-white">
        <div className="container px-5 md:py-24 py-12 mx-auto">
          <div className="mx-auto flex flex-wrap">
            <div style={{ width: "600px" }}>
              <Carousel className={"w-full"}>
                {product.productImage.map((image, key) => (
                  <div key={key}>
                    <img src={image} alt={"Banner-1"} />
                  </div>
                ))}
              </Carousel>
            </div>
            <div className="lg:w-1/2 w-full lg:pl-10 lg:py-6 mt-6 lg:mt-0">
              <h1 className="text-gray-900 text-3xl title-font font-medium mb-1">
                {product.productName}
              </h1>
              <span className="title-font font-medium text-2xl text-red-500">
                <SimplePricing value={product.productPrice} />
              </span>
              <div className="flex my-auto mt-6">
                <p className="leading-relaxed">{product.productDescription}</p>
              </div>
              <div className="flex my-auto mt-6">
                <div className={"ml-auto my-auto flex gap-2"}>
                  <MainButton
                    onClick={() => {
                      window.open(product.sellerContact, "_blank");
                    }}
                    label={
                      <span>
                        Kontak penjual
                        <i className="fa-brands fa-whatsapp px-1"></i>
                      </span>
                    }
                  />
                  {product.sellerInstagram ? (
                    <MainButton
                      onClick={() => {
                        window.open(product.sellerInstagram, "_blank");
                      }}
                      label={<i className="fa-brands fa-instagram px-1"></i>}
                    />
                  ) : null}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};
export default DetailProduct;
